import { Container, Grid } from "@material-ui/core"
import React from "react"
import { blue } from "@material-ui/core/colors"
import FacebookIcon from "@material-ui/icons/Facebook"
import InstagramIcon from "@material-ui/icons/Instagram"
import LinkedInIcon from "@material-ui/icons/LinkedIn"
import { Link, graphql } from "gatsby"
import Logo from "../images/HomeIcon2.png"

export default function Footer() {
  return (
    <footer >
      <Container>
        <div>
          <div>
            <Link href="/" alt="home">
              {" "}
              <img
                className="navbar-logo"
                src={Logo}
                alt="BBI long logo"
              />{" "}
            </Link>
          </div>
          <Grid container className="center">
            <Grid item xs={12} sm={4}>
              <ul>
                <li>
                  <a>ABOUT US</a>
                </li>
                <li>
                  <a>SERVICES</a>
                </li>
              </ul>
            </Grid>
            <Grid item xs={12} sm={4}>
              <ul>
                <li>
                  <a>CARRIERS</a>
                </li>
                <li>
                  <a>JOIN OUR TEAM</a>
                </li>
              </ul>
            </Grid>
            <Grid item xs={12} sm={4}>
              <ul>
                <li>
                  <a>GET A QUOTE</a>
                </li>
                <li>
                  <a>CONTACT US</a>
                </li>
              </ul>
            </Grid>
          </Grid>
          <Grid container className="icons center">
            <Grid >
              <a
                href="https://www.linkedin.com/company/bbi-logistics"
                target="_blank"
              >
                <LinkedInIcon style={{ color: blue[800] }} fontSize="large" />
              </a>
              <a href="https://www.instagram.com/bbilogistics/" target="_blank">
                <InstagramIcon style={{ color: blue[800] }} fontSize="large" />
              </a>
              <a href="https://www.facebook.com/BBILogistics" target="_blank">
                <FacebookIcon style={{ color: blue[800] }} fontSize="large" />
              </a>
            </Grid>
          </Grid>
        </div>
      </Container>
    </footer>
  )
}
